<template>
    <div class="container-fluid">
        <TopButton title="" />
        <!--    -->
        <div class="menu-swiper">
            <!-- Swiper  -->
            <swiper :slides-per-view="4" :space-between="30" grabCursor="true">
                <swiper-slide
                    v-for="(item, index) in menuItems"
                    :key="index"
                    :class="{ active: activeTab === index }"
                    @click="setActiveTab(index)"
                >
                    {{ item.name }}
                </swiper-slide>
            </swiper>
        </div>
        <div class="tab-content">
            <!--  activeTab    -->
            <div v-if="menuItems[activeTab]">
                {{ menuItems[activeTab].content }}
            </div>
        </div>
        <div class="container text-center">
            <div class="row">
                <div class="col-auto me-auto">
                    <div class="form-check">
                        <input class="form-check-input" type="checkbox" value="" id="flexCheckChecked" checked />
                        <label class="form-check-label" for="flexCheckChecked">    </label>
                    </div>
                </div>
                <div class="col-auto">
                    <div class="dropdown">
                        <button
                            class="btn btn-secondary dropdown-toggle"
                            type="button"
                            data-bs-toggle="dropdown"
                            aria-expanded="false"
                        >
                            
                        </button>
                        <ul class="dropdown-menu">
                            <li><button class="dropdown-item" type="button"></button></li>
                            <li><button class="dropdown-item" type="button"></button></li>
                            <li><button class="dropdown-item" type="button"></button></li>
                            <li><button class="dropdown-item" type="button"></button></li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>

        <div class="contents" v-if="activeTab === 0">
            <div class="itemBox" v-for="(items, i) in item" :key="i" @click="goToDetail(items.id)">
                <div class="itemImg">
                    <img :src="items.image" alt="img1" />
                </div>
                <div class="itemText">
                    <p>#000#000#000</p>
                    <p>4.3</p>
                    <p>{{ items.title }}</p>
                    <p>{{ items.price }}</p>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import TopButton from '../components/TopButton.vue';
import CategoryItem from '../assets/js/CategoryItem.js';
import { Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/swiper-bundle.css';

export default {
    name: 'Ingredient',
    components: {
        TopButton,
        Swiper,
        SwiperSlide
    },
    data() {
        return {
            item: CategoryItem,
            activeTab: 0,
            menuItems: [
                { name: '', content: '     .' },
                {
                    name: '',
                    content:
                        ' , , , ,           .'
                },
                {
                    name: '',
                    content:
                        '      ,       .'
                },
                { name: '', content: '.' },
                { name: '', content: '.' },
                { name: '', content: '.' },
                { name: '', content: '.' },
                { name: '', content: '.' }
            ]
        };
    },
    methods: {
        setActiveTab(index) {
            this.activeTab = index;
        },
        goToDetail(items) {
            this.$router.push({
                name: 'ProductPage',
                params: {
                    id: items,
                    image: items.image,
                    title: items.title,
                    price: items.price
                }
            });
        }
    }
};
</script>
<style scoped>
/*   */
.menu-swiper {
    margin: 15px 0;
}
.swiper-slide.active {
    background: #d1d1d1;
    border-radius: 20px;
}
.tab-content {
    margin-top: 20px;
    padding: 10px;
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 10px;
}
/*   */
.contents {
    margin: 0 auto;
    max-width: 390px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.itemBox {
    margin-top: 10px;
    width: 170px;
    height: 260px;
}
.itemBox .itemImg {
    margin: 0 auto;
    width: 170px;
    height: 170px;
    background-color: #d1d1d1;
}
.itemBox .itemImg img {
    display: block;
    width: 170px;
    height: 170px;
}
.itemBox .itemText p {
    text-align: left;
    margin: 0;
    font-size: 0.85em;
}
</style>
